body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
nav.navbar.navbar-default {
  margin-bottom: 0;
  
}
nav.navbar.color{
  color: #a0b603;
}

.footer {
  min-height: 100px;
  background: #e5e5e5;
  position: relative;
}

.footer span {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: #a0b603;
}

.card {
  width: 30%;
  height: 300px;
  font-weight: bold;
  color: white;
  background: grey;
  border-radius: 5px;
  background-image: linear-gradient();
  background-image: url(https://drscdn.500px.org/photo/435236/q%3D80_m%3D1500/v2?webp=true&sig=67031bdff6f582f3e027311e2074be452203ab637c0bd21d89128844becf8e40);
  background-size: cover;
  background-position: center center;
  box-shadow: 0px 10px 30px -5px rgba(0, 0, 0, 0.3);
  transition: box-shadow 0.5s;
  will-change: transform;
  border: 10px solid white;
  margin: auto;
}

.card:hover {
  box-shadow: 0px 30px 100px -10px rgba(0, 0, 0, 0.4);
}

.backImg {
  height: 100vh;
  background-image: url("https://res.cloudinary.com/dddtjci2s/image/upload/v1590432043/rectangular-green-swiss-cheese-leafed-plant-photo-mounted-on-1534924_afxa1k.jpg");
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size:cover;

  }
  .space{
    margin-top: 80px;
    margin-bottom: auto;
}

.pad{
  margin-top: 3rem;
}

.work-thumb{
  display:block;
  position:relative
}
.work-thumb img{
  border:10px solid #fff;
}
.work-thumb .work-text{
  text-align:center;
  position:absolute;
  top:50%;
  left:50%;
  width:90%;
  margin-top:20px;
  -webkit-transition:.3s all ease;
  -o-transition:.3s all ease;
  transition:.3s all ease;
  opacity:0;visibility:hidden;
  -webkit-transform:translate(-50%, -50%);
  -ms-transform:translate(-50%, -50%);
  transform:translate(-50%, -50%)
}
.work-thumb .work-text h2{
  color:#fff;
  font-weight:300;
  margin-bottom:0;
  font-size:1.5rem
}.work-thumb .work-text p{
  color:rgba(255, 255, 255, 0.5)
}
.work-thumb:before{
  content:"";
  background:rgba(0, 0, 0, 0.5);
  position:absolute;
  -webkit-transition:.3s all ease;-o-transition:.3s all ease;transition:.3s all ease;
  opacity:0;
  visibility:hidden;
  top:10px;left:10px;right:10px;bottom:10px
}
.work-thumb:hover .work-text{
  margin-top:0px;
  opacity:1;
  visibility:visible
}
.work-thumb:hover:before{
  opacity:1;
  visibility:visible
}

